
import CHeader from '@/components/x-header'
import researchApi from '@/api/research'
import { go } from '@/libs/router'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

const PURCHASE_MR_TYPE = 1
const SALE_MR_TYPE = 2
export default {
components: {
FlexboxItem,
Flexbox,
XButton,
CHeader
},
data() {
return {
mrType: 0,
data: {}
}
},
created() {
if (this.$route.path.indexOf('purchase') > 0) {
this.mrType = PURCHASE_MR_TYPE;
} else {
this.mrType = SALE_MR_TYPE
}
},
mounted() {
this.fetchData()
},
methods: {
async getMarketList(params, callback) {
params.dataType = this.mrType
let resp = await researchApi.getResearchMarketBaseConfig(params)
if (resp && resp.body && resp.body !== null) {
let respBody = resp.body
if (this.apiOk(respBody)) {
let respData = this.apiData(respBody)
if (callback) {
callback(respData.data)
}
} else {
this.toast('获取市调市场失败')
}
} else {
this.toast('获取市调市场请求失败')
}
},
fetchData() {
let me = this
this.getMarketList({}, function (data) {
if (data.rows) {
if (data.rows.length === 0) {
me.toast('没有配置市调市场')
} else {
me.data = data.rows
}
} else {
me.toast('获取市调市场失败')
}
})
},
onClickMarket(item) {
if (item && item.id && (item.id + '') > '0') {
this.$LStorage.setItem('mr_market', item)
if (this.mrType === PURCHASE_MR_TYPE) {
go('/purchase/market/task', this.$router)
} else {
go('/sale/market/task', this.$router)
}
} else {
this.toast('该市场信息异常')
}
}

}
}



